import { Component, ViewChild } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-catalogodiploma-create',
  templateUrl: 'catalogodiploma-create.html',
})
export class CatalogodiplomaCreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;

  public enc1                 : any;
  public enc2                 : any;
  public enc3                 : any;
  public enc4                 : any;

  public acceso		     : any;
  public unidad		     : any;

  public cue1                 : any;
  public cue2                 : any;
  public cue3                 : any;
  public cue4                 : any;

  public imagen		     : any;
  public estado		     : any;
  idempresa:any;
  public vgrupo  :any=[];
  private _ID                  : String;
  public pageTitle           	: string;
  public image              	: any;

  isDisablednit :any;
  constructor( public storage: Storage, private _IMAGE       : ImageProvider, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
       nombre     : ['', Validators.required],

       enc1     : [''],
       enc2     : [''],
       enc3     : [''],
       enc4     : [''],
       cue1     : [''],
       cue2     : [''],
       cue3     : [''],
       cue4     : [''],


       estado     : ['', Validators.required],
       imagen     : ['']

     });
     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value


  })



   //  this.vgrupo=[{"id":"USAC","nombre":"USAC"},{"id":"BUSES","nombre":"BUSES"},{"id":"TICKES","nombre":"TICKES"}];

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {

    this.acceso = this.navParams.data.acceso;
    this.unidad=this.acceso
    if(this.acceso=='ADMINISTRADOR')
    {

      this.unidad='CURSOSLIBRES'

    }


    if(this.navParams.get("record")==null)
    {


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      this.isDisablednit =true
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;

       this.enc1		=	this.navParams.data.record.encabezado1;
       this.enc2		=	this.navParams.data.record.encabezado2;
       this.enc3		=	this.navParams.data.record.encabezado3;
       this.enc4		=	this.navParams.data.record.encabezado4;

       this.cue1		=	this.navParams.data.record.cuerpo1;
       this.cue2		=	this.navParams.data.record.cuerpo2;
       this.cue3		=	this.navParams.data.record.cuerpo3;
       this.cue4		=	this.navParams.data.record.cuerpo4;

       this.image	=	this.navParams.data.record.foto;
       this.imagen 	=	this.navParams.data.record.foto;
       this.estado		=	this.navParams.data.record.estado;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = {
      idempresa:this.idempresa,
     nombre : this.form.controls['nombre'].value,
     encabezado1 : this.form.controls['enc1'].value,
     encabezado2 : this.form.controls['enc2'].value,
     encabezado3 : this.form.controls['enc3'].value,
     encabezado4 : this.form.controls['enc4'].value,
     unidad : this.unidad,
     cuerpo1 : this.form.controls['cue1'].value,
     cuerpo2 : this.form.controls['cue2'].value,
     cuerpo3 : this.form.controls['cue3'].value,
     cuerpo4 : this.form.controls['cue4'].value,

     foto : this.imagen,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' catalogodiplomas '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'catalogodiplomas').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'catalogodiplomas').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  updateProfileImage() {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {

     this._IMAGE

     .takePhotograph()
     .then((image)=>
     {
        this.imagen   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{

     this.fileInput.nativeElement.click();

   }
  }


  selectImage() : void
  {
   if (Camera['installed']()) {

     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.imagen  	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{

     this.fileInput.nativeElement.click();

   }
  }

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this.imagen =readerEvent.target.result;

   };
      reader.readAsDataURL(event.target.files[0]);
    }
}


}



